@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');

html, body {
    height: 100vh;
    background-color: #2b2b2b !important;
    font-family: 'Montserrat', sans-serif !important;
}

.cover-full {
    display: flex;
    height: 100vh;
}

.to-normal-size {
    width: 55rem;
    background-color: #383838 !important;
    color: #cad2cf;
    transition: box-shadow 0.50s ease-in-out,
}
.animate-main {
    box-shadow: -15px 8px #fff !important;
}

.stable-width {
    width: 16rem;
    color: #cad2cf;
    box-shadow: none !important;
}

.col-xs-2 .btn-elegant {
    background-color: #3a3a3a !important;
}

.counter {
    width: 10rem;
    color: #cad2cf;
    background-color: #3a3a3a;
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;
}

.full-width {
    width: 100%;
}

.app-color {
    color: #cad2cf;
}

.rounded-borders { 
    border-radius: 100px;
}

.text-blacktheme {
    background-color: #3d4146 !important;
    color: #fff !important;
}

.texter-width {
    width: 35rem !important;
}

.mt-8 {
    margin-top: 8rem !important;
}

.mt-10 {
    margin-top: 5rem !important;
}

.card-header-black {
    background-color: #3a3a3a !important;
}
.card-body-black {
    background-color: #383838 !important;
}

.audio-width {
    width: 17rem;
}
